<template>
  <div class="modal fade" id="copyPriceInputModal" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel">復製報價單</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          
            <div class="modal-form-wrapper">
                <form>

                    <div class="form-group">
                        <label for="num">所屬客戶</label>
                        <input type="text" class="form-control" disabled v-model="member_name">
                    </div>
                    <div class="form-group">
                        <label for="num">原報價單號</label>
                        <input type="text" class="form-control" disabled v-model="price_num">
                    </div>
                    <div class="form-group">
                        <label for="num">
                            現報價單號&nbsp;<span class="txt-danger">*</span>
                        </label>
                        <input type="text" class="form-control" id="num" placeholder="報價單號" v-model="num">
                        <small id="numTip" class="form-text text-muted">{{ tip_num }}</small>
                    </div>
                    <div class="form-group">
                        <label for="num">
                            現付款期限&nbsp;<span class="txt-danger">*</span>
                        </label>
                        <input type="text" class="form-control" id="timed" placeholder="yyyy-mm-dd" v-model="timed">
                        <small id="numTip" class="form-text text-muted">{{ tip_timed }}</small>
                        
                    </div>
                </form>
            </div>

            <prices-copy ref="copyDoREF" @refresh_Father="refresh"></prices-copy>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
          <button type="button" class="btn btn-main" @click="sureSubmit">確認復製</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PricesCopy from './PricesCopy.vue'
import SimpleInput from '@/components/Input/Simple/SimpleInput.vue'

import conn from '@/service/conn'
import conn_util from '@/common/es/conn_util'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

export default {
  components: { PricesCopy },
  name: 'SubmitModal',
  data() {
      return {
            num: '',
            tip_num: '',

            timed: '',
            tip_timed: '',

            activeId: null,
            activePc: null,

            price_num: '',
            member_name: ''
      }
  },
  props: {
      
  },
  methods: {
    loadCopy(id) {
        this.activeId = id
        this.loadPriceCollect()
        
        console.log('load Copy')
        tool.showModal('copyPriceInputModal')
    },
    sureSubmit() {
        const res = this.validateForm()
        if (res) {
            this.$refs.copyDoREF.copy(this.activePc, this.num, this.timed)
        }
    },
    // 
    tipClose(area) {
      setTimeout(() => {
        if (area == 'num') {
          this.tip_num = ''
        }
        if (area == 'timed') {
          this.tip_timed = ''
        }
      }, 4200)
    },
    tipForm(res, area) {
      let show = ''
      if (res != true) {
        show = res
      }
      // 
      if (area == 'num') { this.tip_num = show }
      if (area == 'timed') { this.tip_timed = show }

      this.tipClose(area)

    },
    validateForm() {
      let allow = true 
      
      let res = validate.val_empty(this.num, '報價單號')
      if (res == true) {
        res = validate.val_char(this.num, '報價單號')
        this.tipForm(res, 'num')
      } else { this.tipForm(res, 'num') }
      if (res != true) { allow = false }
      
      res = validate.val_empty(this.timed, '現付款期限')
      if (res == true) {
        res = validate.val_timed(this.timed, '現付款期限')
        this.tipForm(res, 'timed')
      } else { this.tipForm(res, 'timed') }
      if (res != true) { allow = false }

      // 
      if (this.num == this.price_num) {
          this.tipForm('請填寫不同的報價單號', 'num')
          allow = false
      }
      const ot = tool.timed.ser_timed(this.activePc.price_collect.start_time, false, true)
      const nt = tool.timed.ser_timed(this.timed, false, true)
      if (ot == nt) {
          this.tipForm('請填寫不同的付款期限', 'timed')
          allow = false
      }
    
      return allow
    },

    loadPriceCollect() {    

        if (this.activeId) {
        
            conn.prices_content.getOneByPrices({
                price_collect: this.activeId
            }).then(pc => {

                if (pc.length > 0) {
                    this.activePc = pc[0]

                    const p = this.activePc.price_collect

                    this.num = p.num
                    this.timed = tool.timed.ser_timed(p.start_time, false, true)
                    this.price_num = p.num
                    this.member_name = p.membery.num + ' ' + p.membery.named
                }

            }).catch(err => { tool.showToast('網絡連接失敗。') })
        }
    },

    refresh() {
      this.$emit('refresh_Father')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


