<template>
    <div></div>
</template>

<script>
import config from '@/common/config'
import tool from '@/common/es/tool'
import conn from '@/service/conn'

    export default {
        name: '',
        data() {
            return {
                active_pc: null,

                active_num: null,

                active_time: null
            }
        },
        props: {
            
        },
        methods: {
            valideSame(num) {
                if (num) {
                    conn.prices.getList_Prices({
                        num 
                    }, true).then(res => {
                        
                        if (res.length > 0) {

                            tool.showToast('已存在相同的報價單編號！！！')
                        } else {

                            this.plusPrice()
                        }
                    })
                }
            },

            

            copy(pc, num, timed) {

                this.active_pc = pc
                this.active_num = num
                this.active_time = timed

                this.valideSame(num)
            },

            plusPrice() {

                let data = this.active_pc.price_collect

                data.num = this.active_num
                data.membery_id = data.membery.id
                data.start_time = this.active_time + ' 00:00:00'
                data.pay_time_id = data.pay_time.id

                delete data.id
                delete data.membery
                delete data.add_time
                delete data.end_time
                delete data.pay_time

                // console.log('data =', data)
                // return
                conn.prices.add(data).then(res => {
                    
                    if (conn.judge_response(res)) {
                        
                        conn.prices_content.add(
                            {
                                price_collect_id: res.id,
                                content: this.active_pc.content
                            }
                        ).then(res_pc => {

                            if (conn.judge_response(res_pc)) {

                                tool.showToast('報價單復製成功！！！')
                                tool.closeAllModal()
                                this.$emit('refresh_Father')
                            }
                        })
                    }
                })
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


